<template>
  <div class="activity">
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell v-for="item in list" :key="item.id" v-if="!(item.status==-3)">
        <div class="listbox">
          <a :href="item.url">
            <div class="banner">
              <img :src="item.picture" />
            </div>
            <div class="title">
              <img class="state" src="../../asset/processing_icon.png" alt v-if="item.status==1" />
              <img class="state" src="../../asset/over_icon.png" alt v-else-if="item.status==3" />
              <img class="state" src="../../asset/unpublished_icon.png" alt v-else-if="item.status==-4" />
              <img class="state" src="../../asset/underreview_icon.png" alt v-else-if="item.status==-3" />
              <img class="state" src="../../asset/fail_icon.png" alt v-else-if="item.status==-2" />
              <img class="state" src="../../asset/notstarted_icon.png" alt v-else-if="item.status==0" />
              <img class="state" src="../../asset/timeout_icon.png" alt v-else-if="item.status==2" />
              <span class="title_text">{{item.name}}</span>
            </div>
            <div class="activity_time">
              <span>活动时间 :</span>
              <span class="time">{{item.formatDate}}</span>
            </div>
          </a>
        </div>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { List, Cell } from "vant";
import axios from "axios";
export default {
  components: {
    [List.name]: List,
    [Cell.name]: Cell
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false
    };
  },

  methods: {
    onLoad() {
      // 异步更新数据
      this.loading = true;
      axios
        .post(this.VUE_APP_API + "getActList.do", {
          params: this.list.length
        })
        .then(response => {
          console.log("response", this.list);
          if (this.list.length == 0) {
            let regDate = /(\d{4})\-(\d{2})\-(\d{2})/;
            response.data.data.forEach((item, index, array) => {
              let startTime = item.startTime.replace(regDate, "$1年$2月$3日");
              let endTime = item.endTime.replace(regDate, "$2月$3日");
              item.formatDate = startTime + "-" + endTime;
              this.list.push(item);
            });
            this.list = response.data.data;
          }
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          if (this.list.length >= 1) {
            this.finished = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="less" scoped>
.van-cell:first-child {
  padding-top: 12px !important;
}
.van-cell {
  padding: 0px 12px 8px 12px;
}
.van-cell__value.van-cell__value--alone {
  width: 100%;
  height: 216px;
  border-radius: 5px;
  box-shadow: 2px 2px 5px #c9c9c9;
  height: 217px;
  /*background-image: url("../../asset/list_box.png");*/
  /*background-size: 376px 217px;*/
  /*background-repeat: no-repeat;*/
  /*background-position: -2px 2px;*/
  .banner {
    width: 100%;
    height: 157px;
    margin-bottom: 11px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .title {
    width: 100%;
    height: 20px;
    .state {
      float: left;
      width: 46px;
      height: 20px;
      margin: 0 9px 0 12px;
    }
    .title_text {
      float: left;
      height: 20px;
      line-height: 20px;
      font-size: 15px;
      font-family: "PingFang";
      color: #000;
    }
  }
  .activity_time {
    height: 28px;
    line-height: 28px;
    padding-left: 12px;
    color: rgb(78, 78, 78);
    font-size: 10px;
    span:first-child {
      display: inline-block;
      width: 46px;
      text-align: center;
    }
    .time {
      margin-left: 9px;
    }
  }
}
</style>